.roadmap {
	padding: 180px 0 185px;
	color: $white;
	@include md {
		padding: 152px 0 54px; }
	&__head {
		margin-bottom: 138px;
		@include md {
			margin-bottom: 111px; } }
	&__title {
		line-height: 1;
		@include md {
			font-size: 54px; } }
	&__description {
		font-size: 48px;
		font-weight: 600;
		@include md {
			font-size: 36px;
			line-height: 1; } }
	&__wrap {
		position: relative; }
	&__row {
		@include clr;
		&:not(:first-child) {
			margin-top: 94px; } }
	&__col {
		padding-left: 17px;
		width: 50%;
		display: table;
		min-height: 280px;
		@include md {
			min-height: 186px; }
		.roadmap__row:nth-child(odd) & {
			padding-left: 40px;
			float: right; } }
	&__vert {
		position: relative;
		padding-left: 70px;
		vertical-align: top;
		@include f(18px,300,2);
		display: table-cell;
		@include md {
			padding-left: 50px;
			font-size: 14px;
			line-height: 1.6; } }
	&__text {
		max-width: 360px; }
	&__icon {
		position: absolute;
		top: 5px;
		left: 0;
		.icon {
			font-size: 48px;
			@include md {
				font-size: 36px; } }
		img {
			display: block; } }
	&__title-sm {
		margin-bottom: 6px;
		font-family: $montserrat;
		@include f(36px,600,1.1);
		@include md {
			font-size: 27px; } } }
